import React, { useState } from 'react';
import { AuthService } from '../services/authService';
import { User } from '../types';

interface AuthModalProps {
  isOpen: boolean;
  onClose: () => void;
  onLoginSuccess: (user: User) => void;
}

const AuthModal: React.FC<AuthModalProps> = ({ isOpen, onClose, onLoginSuccess }) => {
  const [activeTab, setActiveTab] = useState<'login' | 'register'>('login');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  if (!isOpen) return null;

  const handleGoogleLogin = async () => {
      setIsLoading(true);
      setError('');
      try {
          setTimeout(async () => {
              const mockGoogleUser = {
                  email: `user_${Math.floor(Math.random() * 1000)}@gmail.com`,
                  name: 'Google Kullanicisi',
                  avatar: 'https://lh3.googleusercontent.com/a/default-user'
              };
              const user = await AuthService.loginWithGoogle(mockGoogleUser.email, mockGoogleUser.name, mockGoogleUser.avatar);
              onLoginSuccess(user);
          }, 1000);
      } catch (err: any) {
          setError(err.message);
          setIsLoading(false);
      }
  };

  const handleEmailSubmit = async (e: React.FormEvent) => {
      e.preventDefault();
      setIsLoading(true);
      setError('');

      try {
          if (activeTab === 'register') {
              if (!name || !email || !password) throw new Error("Tum alanlari doldurun.");
              const user = await AuthService.registerWithEmail(email, name);
              onLoginSuccess(user);
          } else {
              if (!email || !password) throw new Error("Email ve sifre gerekli.");
              const user = await AuthService.loginWithEmail(email);
              onLoginSuccess(user);
          }
      } catch (err: any) {
          setError(err.message || "Bir hata olustu.");
          setIsLoading(false);
      }
  };

  return (
    <div className="fixed inset-0 z-[70] flex items-center justify-center p-4 bg-black/50 backdrop-blur-sm animate-fade-in">
      <div className="bg-white w-full max-w-md rounded-2xl border border-gray-200 shadow-2xl overflow-hidden relative" onClick={e => e.stopPropagation()}>
        
        <button onClick={onClose} className="absolute top-4 right-4 text-gray-400 hover:text-gray-900">
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path></svg>
        </button>

        <div className="p-8">
            <h2 className="text-2xl font-bold text-gray-900 mb-6 text-center">
                {activeTab === 'login' ? 'KriptoPusula\'ya Giris Yap' : 'Hesap Olustur'}
            </h2>

            <button 
                onClick={handleGoogleLogin}
                disabled={isLoading}
                className="w-full bg-gray-100 text-gray-900 font-medium py-3 rounded-xl flex items-center justify-center gap-3 hover:bg-gray-200 transition-colors mb-6 border border-gray-200"
            >
                <svg className="w-5 h-5" viewBox="0 0 24 24">
                    <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" fill="#4285F4"/>
                    <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" fill="#34A853"/>
                    <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" fill="#FBBC05"/>
                    <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" fill="#EA4335"/>
                </svg>
                Google ile Devam Et
            </button>

            <div className="flex items-center gap-4 mb-6">
                <div className="h-px bg-gray-200 flex-1"></div>
                <span className="text-gray-400 text-sm">veya e-posta ile</span>
                <div className="h-px bg-gray-200 flex-1"></div>
            </div>

            {error && (
                <div className="bg-red-50 border border-red-200 text-red-600 p-3 rounded-lg text-sm mb-4 text-center">
                    {error}
                </div>
            )}

            <form onSubmit={handleEmailSubmit} className="space-y-4">
                {activeTab === 'register' && (
                    <div>
                        <input 
                            type="text" 
                            placeholder="Ad Soyad"
                            value={name}
                            onChange={e => setName(e.target.value)}
                            className="w-full bg-gray-50 border border-gray-200 rounded-xl px-4 py-3 text-gray-900 focus:ring-2 focus:ring-blue-500 outline-none"
                        />
                    </div>
                )}
                <div>
                    <input 
                        type="email" 
                        placeholder="E-posta adresi"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                        className="w-full bg-gray-50 border border-gray-200 rounded-xl px-4 py-3 text-gray-900 focus:ring-2 focus:ring-blue-500 outline-none"
                    />
                </div>
                <div>
                    <input 
                        type="password" 
                        placeholder="Sifre"
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                        className="w-full bg-gray-50 border border-gray-200 rounded-xl px-4 py-3 text-gray-900 focus:ring-2 focus:ring-blue-500 outline-none"
                    />
                </div>

                <button 
                    type="submit" 
                    disabled={isLoading}
                    className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-3 rounded-xl transition-colors disabled:opacity-50"
                >
                    {isLoading ? 'Isleniyor...' : (activeTab === 'login' ? 'Giris Yap' : 'Kayit Ol')}
                </button>
            </form>

            <div className="mt-6 text-center text-sm text-gray-500">
                {activeTab === 'login' ? 'Hesabin yok mu? ' : 'Zaten hesabin var mi? '}
                <button 
                    onClick={() => setActiveTab(activeTab === 'login' ? 'register' : 'login')}
                    className="text-blue-600 hover:text-blue-700 font-medium"
                >
                    {activeTab === 'login' ? 'Kayit Ol' : 'Giris Yap'}
                </button>
            </div>
        </div>
      </div>
    </div>
  );
};

export default AuthModal;
